.hero {
  background-color: #080C3B;
  width: 100%;
  border-bottom: 4px solid $pastel-pink;

  img{ // A revoir ?
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  @include medium {
    display: flex;
    height: 75vh;
  }
}

.hero__newsItem:first-child {
  height: 40vh;
  background-color: #7E82B1;

  @include medium {
    display: flex;
    height: 100%;
    width: 65%;
  }
}

.hero__newsItem:nth-child(2) {

  & > article:first-child {
    height: 30vh;
    background-color: #545993;

    @include medium {
      height: 40%;
    }
  }

  & > article:last-child {
    height: 30vh;
    background-color: #191E58;

    @include medium {
      height: 60%;
    }
  }

  @include medium {
    width: 35%;
  }
}
